              break;
            // BKG-Q2 <-> N5Y-4N
            case 6691:
            case 3003:
              this.isRemoteLocation = true;
              this.volumeReward = 1000 * tmp_m3;
              this.charges_uprice = '1000 ISK/m³';
              this.reward = this.volumeReward;
              break;
            // JITA -> Otsasai
            case 1036:
              this.volumeReward = 300 * tmp_m3;
              this.charges_uprice = '300 ISK/m³';
              collateralReward =  tmp_collateral * 0.01;
              if (this.volumeReward < (tmp_collateral * 0.01)) {
                this.reward = collateralReward;
                this.show_collateral_info = true;
              } else {
                this.reward = this.volumeReward;
              }
              break;
            // Otsasai -> JITA
            case 5806:
              this.volumeReward = 300 * tmp_m3 + (tmp_collateral * 0.02);
              this.charges_uprice = '300 ISK/m³ + 保证金2%';
              this.reward = this.volumeReward;
              break;
            // 4-HWWF <-> Otsasai
            case 5829:
            case 3947:
              this.volumeReward = 500 * tmp_m3;
              this.charges_uprice = '500 ISK/m³';
              this.reward = this.volumeReward;
              break;

            // 4-HWWF <-> U-WLT9
            case 3969:
            case 7603:
              this.volumeReward = 500 * tmp_m3;
              this.charges_uprice = '500 ISK/m³';
              this.reward = this.volumeReward;
              break;
            // RF-X7V -> Otsasai
            // case 2021:
            //   this.volumeReward = 1700 * tmp_m3;
            //   this.charges_uprice = '1700 ISK/m³';
            //   collateralReward =  tmp_collateral * 0.01;
            //   if (this.volumeReward < (tmp_collateral * 0.01)) {
            //     this.reward = collateralReward;
            //     this.show_collateral_info = true;
            //   } else {
