            //   }
            //   break;
            // JITA -> OSY-UD
            // case 1079:
            //   this.volumeReward = 1300 * tmp_m3;
            //   this.charges_uprice = '1300 ISK/m³';
            //   collateralReward =  tmp_collateral * 0.01;
            //   if (this.volumeReward < (tmp_collateral * 0.01)) {
