            //     this.show_collateral_info = true;
            //   } else {
            //     this.reward = this.volumeReward;
            //   }
            //   break;
            // OSY-UD -> JITA
            // case 10223:
            //   this.volumeReward = 800 * tmp_m3 + (tmp_collateral * 0.02);
            //   this.charges_uprice = '800 ISK/m³ + 保证金2%';
            //   this.reward = this.volumeReward;
            //   break;
            // JITA -> T5ZI-S
            // case 1090:
            //   this.isRemoteLocation = true;
            //   this.volumeReward = 3500 * tmp_m3;
            //   this.charges_uprice = '3500 ISK/m³';
            //   collateralReward =  tmp_collateral * 0.01;
            //   if (this.volumeReward < (tmp_collateral * 0.01)) {
            //     this.reward = collateralReward;
            //     this.show_collateral_info = true;
            //   } else {
            //     this.reward = this.volumeReward;
            //   }
            //   break;
            // // T5ZI-S -> JITA
            // case 11032:
            //   this.isRemoteLocation = true;
            //   this.volumeReward = 2400 * tmp_m3 + (tmp_collateral * 0.02);
            //   this.charges_uprice = '2400 ISK/m³ + 保证金2%';
            //   this.reward = this.volumeReward;
            //   break;
            // // 4-HWWF <-> T5ZI-S
            // case 4001:
            // case 11055:
            //   this.isRemoteLocation = true;
            //   this.volumeReward = 4200 * tmp_m3;
            //   this.charges_uprice = '4200 ISK/m³';
            //   collateralReward =  tmp_collateral * 0.01;
            //   if (this.volumeReward < (tmp_collateral * 0.01)) {
            //     this.reward = collateralReward;
            //     this.show_collateral_info = true;
            //   } else {
            //     this.reward = this.volumeReward;
            //   }
            //   break;
            // RF-X7V <-> T5ZI-S
            // case 2075:
            // case 11039:
            //   this.isRemoteLocation = true;
