            //     this.show_collateral_info = true;
            //   } else {
            //     this.reward = this.volumeReward;
            //   }
            //   break;
            // P-ZMZV -> RF-X7V
            // case 11039:
            //   this.volumeReward = 1000 * tmp_m3 + (tmp_collateral * 0.02);
            //   this.charges_uprice = '1000 ISK/m³ + 保证金2%';
            //   this.reward = this.volumeReward;
            //   break;
              // JITA -> YZ9-F6
            // case 1089:
            //   this.volumeReward = 1700 * tmp_m3;
            //   this.charges_uprice = '1700 ISK/m³';
            //   collateralReward =  tmp_collateral * 0.01;
            //   if (this.volumeReward < (tmp_collateral * 0.01)) {
            //     this.reward = collateralReward;
            //     this.show_collateral_info = true;
            //   } else {
            //     this.reward = this.volumeReward;
            //   }
            //   break;
            // // YZ9-F6 -> JITA
            // case 11847:
            //   this.volumeReward = 700 * tmp_m3 + (tmp_collateral * 0.02);
            //   this.charges_uprice = '700 ISK/m³ + 保证金2%';
            //   this.reward = this.volumeReward;
            //   break;
            //   // JITA -> YZ9-F6
            // case 2074:
            //   this.volumeReward = 2400 * tmp_m3;
            //   this.charges_uprice = '2400 ISK/m³';
            //   collateralReward =  tmp_collateral * 0.01;
            //   if (this.volumeReward < (tmp_collateral * 0.01)) {
            //     this.reward = collateralReward;
            //     this.show_collateral_info = true;
            //   } else {
            //     this.reward = this.volumeReward;
            //   }
            //   break;
            // // YZ9-F6 -> JITA
            // case 11854:
            //   this.volumeReward = 1000 * tmp_m3 + (tmp_collateral * 0.02);
            //   this.charges_uprice = '1000 ISK/m³ + 保证金2%';
            //   this.reward = this.volumeReward;
            //   break;
            default:
              this.reward = '--';
              this.charges = '--';
              if (!this.no_routing_warning_flag) {
                this.no_routing_warning_flag = true;
                this.$notify({
                  title: '此航线无报价',
                  message: '当前航线无顺丰报价！',
                  type: 'warning',
                  onClose: this.no_routing_warning_closed
                });
              }
              return;
          }
  
          this.extend_fee = 0;
          // 保证金额外保险费计算
          if (tmp_collateral > 5000000000 && this.cal_extend_fee && this.reward < (((tmp_collateral - 5000000000) * 0.02) + this.volumeReward)) {
            this.reward = this.volumeReward;
            this.extend_fee = (tmp_collateral - 5000000000) * 0.02;
            this.charges = '合同（高保证金），运费标准：体积运费 + 保证金超过5B部分的2%；体积运费标准：';
            this.show_collateral_info = false;
          }

          if (this.isRemoteLocation) {
            this.completeDays = '7 Days';
          }

          if (this.reward != '--' && this.reward < 5000000) {
            this.reward = 5000000;
            this.charges = '合同，运费不足500万时按500万计费，运费标准：';
            if (!this.fare_low_info_flag) {
              this.fare_low_info_flag = true;
              this.$notify.info({
                title: '最低运费限制',
                message: '体积运费不足500万时按500万计费！',
                onClose: this.fare_low_info_closed
              });
            }
          } else if (this.show_collateral_info) {
            this.showCollateredInfoNotify(this.reward);
          }
          if (this.extend_fee > 0) {
            this.reward = this.reward + this.extend_fee;

          }
          this.reward = (this.reward.toFixed() + '').replace(/(\d{1,3})(?=(\d{3})+(?:$|\.))/g,'$1,');
          this.charges = this.contract_type_cn + this.charges + this.charges_uprice;
        } else {
          this.reward = '--';
          this.charges = '--';
        }
      },
      m3_notify_closed: function() {
        this.m3_error_flag = false;
      },
      collateral_info_closed: function() {
        this.collateral_info_flag = false;
      },
      no_routing_warning_closed: function() {
        this.no_routing_warning_flag = false;
      },
      fare_low_info_closed: function() {
        this.fare_low_info_flag = false;
      },
      showCollateredInfoNotify: function (val) {
        if (!this.collateral_info_flag && val > 5000000) {
          this.collateral_info_flag = true;
          this.$notify.info({
            title: '高保证金低体积运费',
            message: '体积运费不足保证金1%，运费按保证金1%计费！',
            onClose: this.collateral_info_closed
          });
        }
        this.charges = '合同（低体积高保证金——体积运费不足保证金1%），运费标准：';
        this.charges_uprice = '保证金的1%'
        // this.cal_extend_fee = false;
      }
      
    }
  }
  
</script>

<style>
