  }
  .el-main, .el-footer {
    background-color: #E9EEF3;
    color: #333;
    text-align: center;
    line-height: 360px;
  }
  .el-row {
    margin-bottom: 1px;
    line-height: 40px;
  }
  .el-col {
    border-radius: 4px;
  }
  .bg-purple-dark {
    background: #99a9bf;
  }
  .bg-purple {
    background: #d3dce6;
  }
  .bg-purple-light {
    background: #e5e9f2;
  }
  .grid-content {
    border-radius: 4px;
    min-height: 36px;
  }
  .row-bg {
    padding: 10px 0;
    background-color: #f9fafc;
  }
  .el-select {
    width: 500px;
  }
  .el-input {
    width: 500px;
  }
</style>



